.carousel{
    background: url(../../../../image/home-bg.jpg)no-repeat;
}
.Home-img{
    max-width: 100%;
    text-align: center;
}
.carousel-caption .home-title{
    color: #000;
    margin-top: 1.5rem;
}
.home-par{
    color: #000;
    margin-top: 1.5rem;

}
.box-content{
  float: right;
  margin: 10rem;
}
.box-content h3{
    color: #6c757d;
    font-size: 1rem;
    font-weight: 500;
}
.carousel-control-next-icon, .carousel-control-prev-icon{
    background-color: #6c757d;
    padding: 0.5rem;
    border-radius: 3px;
    color: #000;

}
.box{
    background: url(../../../../image/banner-1.jpg) no-repeat;
    max-width: 100%;
    margin-top: 1.5rem;
    padding: 60px;
}
.box-2{
    background: url(../../../../image/banner-2.jpg) no-repeat;
    max-width: 100%;
    margin-top: 1.5rem;
    padding: 60px;
}
.box-3{
    background: url(../../../../image/banner-3.jpg) no-repeat;
    max-width: 100%;
    margin-top: 1.5rem;
    padding: 60px;
}
@media (max-width: 600px) {
    .box-content{
        float: right;
        margin: 0;
      }
}