.img-contact{
    background: url(../../../../image/heading-bg.jpg) no-repeat;
    padding:150px;
}
.box-cont{
    background-color: #ddd;
    padding: 1rem;
    border-radius: 5px;
}
.box-cont span{
    font-size: 2rem;
    color: #d1b14f;
}
.box-cont h3{
    font-size: 1.5rem;
}
.input-coact{
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    background-color: #ddd;
}
input{
    display: flex;
    justify-content: center;
    flex-direction: column;
    margin-top: 1.3rem;
    padding: 0.5rem;
}